'use client';

import { RemessaInfo } from '@/types/jadlog';

interface DashboardProps {
  remessas: RemessaInfo[];
}

export default function Dashboard({ remessas }: DashboardProps) {
  const stats = {
    emTransito: 0,
    entregue: 0,
    comProblemas: 0
  };

  // Calcular estatísticas baseadas no status real das remessas
  remessas.forEach(remessa => {
    if (remessa.tracking) {
      const status = remessa.tracking.status.toLowerCase();
      if (status.includes('entregue') || status.includes('entrega')) {
        stats.entregue++;
      } else if (status.includes('problema') || status.includes('erro') || status.includes('não encontrado')) {
        stats.comProblemas++;
      } else {
        stats.emTransito++;
      }
    } else {
      // Se não tem tracking ainda, considera em trânsito
      stats.emTransito++;
    }
  });

  return (
    <div className="grid grid-cols-1 md:grid-cols-3 gap-6 mb-8">
      {/* Em Trânsito */}
      <div className="bg-white rounded-lg shadow-sm border-l-4 border-blue-500 p-6">
        <div className="flex items-center">
          <div className="flex-shrink-0">
            <div className="w-12 h-12 bg-blue-100 rounded-lg flex items-center justify-center">
              <svg className="w-6 h-6 text-blue-600" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M8 7h12m0 0l-4-4m4 4l-4 4m0 6H4m0 0l4 4m-4-4l4-4" />
              </svg>
            </div>
          </div>
          <div className="ml-4">
            <p className="text-sm font-medium text-gray-600">Em Trânsito</p>
            <p className="text-3xl font-bold text-gray-900">{stats.emTransito}</p>
          </div>
        </div>
      </div>

      {/* Entregue */}
      <div className="bg-white rounded-lg shadow-sm border-l-4 border-green-500 p-6">
        <div className="flex items-center">
          <div className="flex-shrink-0">
            <div className="w-12 h-12 bg-green-100 rounded-lg flex items-center justify-center">
              <svg className="w-6 h-6 text-green-600" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M5 13l4 4L19 7" />
              </svg>
            </div>
          </div>
          <div className="ml-4">
            <p className="text-sm font-medium text-gray-600">Entregue</p>
            <p className="text-3xl font-bold text-gray-900">{stats.entregue}</p>
          </div>
        </div>
      </div>

      {/* Com Problemas */}
      <div className="bg-white rounded-lg shadow-sm border-l-4 border-red-500 p-6">
        <div className="flex items-center">
          <div className="flex-shrink-0">
            <div className="w-12 h-12 bg-red-100 rounded-lg flex items-center justify-center">
              <svg className="w-6 h-6 text-red-600" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 9v2m0 4h.01m-6.938 4h13.856c1.54 0 2.502-1.667 1.732-2.5L13.732 4c-.77-.833-1.964-.833-2.732 0L3.732 16.5c-.77.833.192 2.5 1.732 2.5z" />
              </svg>
            </div>
          </div>
          <div className="ml-4">
            <p className="text-sm font-medium text-gray-600">Com Problemas</p>
            <p className="text-3xl font-bold text-gray-900">{stats.comProblemas}</p>
          </div>
        </div>
      </div>
    </div>
  );
}
