'use client';

import { useState } from 'react';
import { RemessaInfo } from '@/types/jadlog';
import UploadXML from '@/components/UploadXML';
import RemessasList from '@/components/RemessasList';
import Dashboard from '@/components/Dashboard';

export default function Home() {
  const [remessas, setRemessas] = useState<RemessaInfo[]>([]);

  const handleUpload = (novasRemessas: RemessaInfo[]) => {
    setRemessas(prev => [...prev, ...novasRemessas]);
  };

  const handleClear = () => {
    setRemessas([]);
  };

  const handleUpdateRemessa = (index: number, remessaAtualizada: RemessaInfo) => {
    setRemessas(prev => {
      const novasRemessas = [...prev];
      novasRemessas[index] = remessaAtualizada;
      return novasRemessas;
    });
  };

  return (
    <main className="min-h-screen bg-gray-50">
      <div className="container mx-auto px-4 py-8">
        {/* Header */}
        <div className="flex items-center mb-8">
          <div className="flex items-center">
            <div className="w-10 h-10 bg-blue-600 rounded-lg flex items-center justify-center mr-3">
              <svg className="w-6 h-6 text-white" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 19v-6a2 2 0 00-2-2H5a2 2 0 00-2 2v6a2 2 0 002 2h2a2 2 0 002-2zm0 0V9a2 2 0 012-2h2a2 2 0 012 2v10m-6 0a2 2 0 002 2h2a2 2 0 002-2m0 0V5a2 2 0 012-2h2a2 2 0 012 2v14a2 2 0 01-2 2h-2a2 2 0 01-2-2z" />
              </svg>
            </div>
            <h1 className="text-2xl font-bold text-gray-900">
              Dashboard de Rastreamento Jadlog
            </h1>
          </div>
        </div>

        {/* Upload Section - Mostrar apenas se não houver remessas */}
        {remessas.length === 0 && (
          <div className="mb-12">
            <div className="text-center mb-8">
              <h2 className="text-xl font-semibold text-gray-900 mb-2">
                Importe seus arquivos XML
              </h2>
              <p className="text-gray-600">
                Carregue os arquivos XML da Jadlog para visualizar as informações das remessas
              </p>
            </div>
            <UploadXML onUpload={handleUpload} />
          </div>
        )}

        {/* Dashboard e Lista - Mostrar apenas se houver remessas */}
        {remessas.length > 0 && (
          <>
            {/* Dashboard Cards */}
            <Dashboard remessas={remessas} />

            {/* Upload compacto quando há remessas */}
            <div className="mb-6">
              <div className="bg-white rounded-lg shadow-sm p-4">
                <div className="flex items-center justify-between">
                  <div>
                    <h3 className="text-sm font-medium text-gray-900">Adicionar mais remessas</h3>
                    <p className="text-xs text-gray-500">Carregue arquivos XML adicionais</p>
                  </div>
                  <div className="flex-shrink-0">
                    <UploadXML onUpload={handleUpload} compact />
                  </div>
                </div>
              </div>
            </div>

            {/* Lista de Remessas */}
            <RemessasList 
              remessas={remessas} 
              onClear={handleClear} 
              onUpdateRemessa={handleUpdateRemessa}
            />
          </>
        )}

        {/* Instructions - Mostrar apenas se não houver remessas */}
        {remessas.length === 0 && (
          <div className="max-w-3xl mx-auto mt-16">
            <div className="bg-white rounded-lg shadow-sm p-8">
              <h3 className="text-xl font-semibold text-gray-900 mb-4">
                Como usar esta ferramenta
              </h3>
              <div className="space-y-4 text-gray-600">
                <div className="flex items-start space-x-3">
                  <div className="flex-shrink-0 w-6 h-6 bg-blue-100 text-blue-600 rounded-full flex items-center justify-center text-sm font-medium">
                    1
                  </div>
                  <p>
                    <strong>Selecione os arquivos XML:</strong> Clique no botão de upload acima e selecione um ou mais arquivos XML fornecidos pela Jadlog.
                  </p>
                </div>
                <div className="flex items-start space-x-3">
                  <div className="flex-shrink-0 w-6 h-6 bg-blue-100 text-blue-600 rounded-full flex items-center justify-center text-sm font-medium">
                    2
                  </div>
                  <p>
                    <strong>Visualize as informações:</strong> Após o upload, você verá um dashboard com estatísticas e uma tabela com os dados das remessas.
                  </p>
                </div>
                <div className="flex items-start space-x-3">
                  <div className="flex-shrink-0 w-6 h-6 bg-blue-100 text-blue-600 rounded-full flex items-center justify-center text-sm font-medium">
                    3
                  </div>
                  <p>
                    <strong>Pesquise e filtre:</strong> Use o campo de pesquisa para encontrar remessas específicas por nome ou CPF do destinatário.
                  </p>
                </div>
              </div>
            </div>
          </div>
        )}
      </div>
    </main>
  );
}
